@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700&display=swap");
:root {
  --primary-heading-color: deeppink;
  --detail-heading-color: #07e2ff;
}

html {
  font-size: 62.5%;
  -webkit-text-size-adjust: 100%;
}

body {
  color: #2e2e2e;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.6;
  overflow-x: hidden;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.kniiife-data {
  background-color: #444;
  color: #fff;
  padding: 1.6rem;
  margin-bottom: 0.8rem;
  line-height: 1.2rem;
}
.kniiife-data__title {
  color: var(--detail-heading-color);
  font-size: 2.4rem;
  line-height: 1.8rem;
}

.header {
  padding: 1.6rem;
  border-bottom: 2px solid #222;
}
.header__headline {
  font-size: 60px;
  color: var(--primary-heading-color);
  font-family: "Bebas Neue", sans-serif;
  margin-bottom: 1.6rem;
}
.header__subheadline {
  font-size: 36px;
  font-weight: 300;
  color: #444;
  margin-top: 0;
}

